<template>
    <div>
    <div id="layout" class="d-flex flex-column">

        <main>
            <div class="content_wrapper">


                <div class="menu_wrapper container-fluid  ">

                    <div class="itdmavatar">
                        <section class="avatar_brick">
                            <!-- <div><img src="img/logo.svg" class="avatar_photo img-circle img-responsive" /></div> -->

                            <div class="text-center"><label class="user">{{ currentuser.fullname }}</label></div>
                            <div class="form-group">
                                <select class="form-control" id="exampleFormControlSelect1" v-model="seldeptname">
                                <option selected>--請選擇組別--</option>
                                <option v-for="(item, index) in deptname" :key="index">{{ item }}</option>
                                </select>
                            </div>
                        </section>
                    </div>
                    <div class="menu col col-sm-8 col-md-7  col-lg-6 col-xl-5">
                        <section class="menu_brick" @click.prevent="openselmenu('D')">
                            <i class="fa fa-check-square-o"  aria-hidden="true"></i>
                            <label for="">日巡檢</label>
                        </section>
                        <section class="menu_brick" @click.prevent="openselmenu('W')">
                            <i class="fa fa-cloud-upload"  aria-hidden="true"></i>
                            <label for="">周巡檢</label>
                        </section>
                        <section class="menu_brick" @click.prevent="openselmenu('M')">
                            <i class="fa fa-cloud-download" aria-hidden="true"></i>
                            <label for="">月巡檢</label>
                        </section>
                    </div>
                    <div class="menu col col-sm-8 col-md-7  col-lg-6 col-xl-5" :class="{'hidden': isShow}">
                        <section class="menu_brick" @click.prevent="getlog"> 
                            <i class="fa fa-check-square-o" aria-hidden="true"></i>
                            <label for="">下載巡檢項目</label>
                        </section>
                        <section class="menu_brick">
                            <i class="fa fa-check-square-o" aria-hidden="true"></i>
                            <label for="">執行巡檢作業</label>
                        </section>
                        <section class="menu_brick">
                            <i class="fa fa-check-square-o" aria-hidden="true"></i>
                            <label for="">上傳巡檢結果</label>
                        </section>
                    </div>
                </div>
            </div>
        </main>
    </div>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
    data() {
        return {
            isShow:true,
            seldeptname:'',
            periodtype:''
        }
    },
    methods: {
        openselmenu (payload) {
            const vm = this;
            vm.periodtype = payload;
            vm.isShow = false;
        },
        getlog (payload) {
            const vm = this;
            const nowtime = new Date().toISOString().split('T');
            const data = {
                deptname: vm.seldeptname,
                periodtype: vm.periodtype,
                logdate: nowtime[0]
            }
            console.log(data)
            const getlogapi = 'https://www.sinetics.com.tw/ITDMLIMSAPI/Inspect/GetLog'
            this.$http.post(getlogapi, data).then((response) => {
                console.log(response.data);
                // vm.getcurrentuser(response.data);
                // vm.$router.push('/SelMenu')
            }).catch((error) => {
                console.log('下載失敗');
            })
        }
    },
    computed: {
        ...mapGetters(['currentuser', 'deptname'])
    },
    created() {

    },
}
</script>